import logo from './logo.svg';
import './App.css';
import React from 'react'
import axios from 'axios'

const products = [
  {
    id:1,
    name:'Veltlínske zelené',
    info:'suché, biele, aov, 12.59% alk',
    price:8.5,
    priceWithTax:10
  },
  {
    id:2,
    name:'Ribezlak',
    info:'ribezlove, 8% alk',
    price:3,
    priceWithTax:5
  }
]

class App extends React.Component{
  render(){
    return <OrderForm></OrderForm>
  }
}

class OrderForm extends React.Component {
  constructor(props){
    super(props);
    this.state = {count:1,order:[]}
    this.addToOrder = this.addToOrder.bind(this)
    this.removeFromOrder = this.removeFromOrder.bind(this)
    this.setCountOfProduct = this.setCountOfProduct.bind(this)
  }

  addToOrder(id){
    const newOrder = this.state.order
    const product = products.find(prod => prod.id===id)
    if (newOrder.find(prod => prod.id===id)){
      newOrder.map(prod=>{
        if (prod.id === id){
          prod.count+=1
        }
        return prod
      })
    }
    else{
      newOrder.push({
        ...product,
        count:1
      })
    }
    this.setState({order:newOrder})
  }

  removeFromOrder(id){
    let newOrder = this.state.order
    newOrder = newOrder.filter(prod=>prod.id!==id)
    this.setState({order:newOrder})
  }

  setCountOfProduct(id,count){
    if (count === 0){
      return this.removeFromOrder(id)
    }
    let newOrder = this.state.order
    newOrder = newOrder.map(prod=>{
     if ( prod.id === id ){
       prod.count = count
     }
     return prod
    })
    this.setState({order:newOrder})
  }
  async componentDidMount(){
    const st = await axios.get('https://marhulky.sk:8443/api/store/products')
    this.setState({count:st.data.products.length})
  }
  render() {
    const productsToRender = products.map(product => {return <Product id={product.id} addToOrder={this.addToOrder} data={product}></Product>})
    const productSummariesToRender = this.state.order.map(product => {return <SummaryProduct
      id={product.id} 
      setCountOfProduct={this.setCountOfProduct}
      removeFromOrder={this.removeFromOrder} 
      data={product}></SummaryProduct>})
    return <div className='order-form'>
      <h2>Objednávka</h2>
      <div className='order-header'>
        <div className='product'>Produkt</div>
        <div className='price'>Cena</div>
      </div>
      {productsToRender}
      <h2>Sumár</h2>
      {productSummariesToRender}
      <PriceSummary order={this.state.order}></PriceSummary>
    </div>
  }
}

class PriceSummary extends React.Component{
  render(){
    const order = this.props.order
    const priceWithoutTax = order.reduce((ac,prod) => {
      return ac+prod.price*prod.count
    },0).toFixed(2)+'€'
    const priceWithTax = order.reduce((ac,prod) => {
      return ac+prod.priceWithTax*prod.count
    },0).toFixed(2)+'€'
    return(
    <div>
    <div className="priceBlock">
      <div>Cena bez DPH</div>
      <div>{priceWithoutTax}</div>
    </div>
    <div className="priceBlock">
      <div>Cena s DPH</div>
      <div>{priceWithTax}</div>
    </div>
    </div>
    )
  }
}

class Product extends React.Component{
  render(){
    return (
      <div><button onClick={()=>this.props.addToOrder(this.props.data.id)}>+</button>
      <h2>{this.props.data.name}</h2>
      <div>{this.props.data.info}</div>
      <div>{this.props.data.price}</div></div>
    )
  }
}

class SummaryProduct extends React.Component{
  render(){
    return(
      <div>
        <h2>{this.props.data.name}</h2>
        <input type='number' min={0} value={this.props.data.count} onChange={(event)=>{
          this.props.setCountOfProduct(this.props.data.id,Number(event.target.value))
          }}></input>
          <button onClick={()=>this.props.removeFromOrder(this.props.data.id)}>X</button>
      </div>
    )
  }
}

export default App;
